import axios from "axios";
import Swal from "sweetalert2";

export const login = async (email, password) => {
  try {
    const res = await axios({
      method: "POST",
      url: "/users/login",
      data: {
        email,
        password,
      },
    });
    Swal.fire({
      position: "center",
      icon: "success",
      title: "Uurla daxil olundu",
      showConfirmButton: false,
      timer: 2500,
    });
    window.setTimeout(() => {
      location.assign("/manageMessages");
    }, 1500);
  } catch (err) {
    Swal.fire({
      icon: "error",
      title: "Xta",
      text: "Email v ya ifr shvdir",
    });

    window.setTimeout(() => {
      location.assign("/login");
    }, 1500);
  }
};
